export function httpGet (path, callback) {
    let location = url.parse(path);

    http.get({
        host: location.host || window.location.host,
        // If path looks like a relative URL, we need to get the full one.
        path: (location.pathname.indexOf('/') !== 0) ? window.location.pathname + location.pathname : location.pathname,
        withCredentials: false
    }, function (response) {
        let body = '';
        response.on('data', function (chunk) {
            body += chunk;
        });
        response.on('end', function () {
            let error = null;
            callback(error, body);
        });
    });
}

export function debounce(func, wait, immediate) {
    let timeout;
    return function() {
        let context = this,
            args = arguments;
        let later = function() {
            timeout = null;
            if (!immediate) {
                func.apply(context, args);
            }
        };
        let callNow = immediate && !timeout;
        clearTimeout(timeout);
        timeout = setTimeout(later, wait);
        if (callNow) {
            func.apply(context, args);
        }
    };
}

export function getPosition(dom) {
    let y = 0,
        x = 0;
    do {
        y += dom.offsetTop || 0;
        x += dom.offsetLeft || 0;
        dom = dom.offsetParent;
    } while (dom);

    return {
        y: y,
        x: x
    };
}

//  Check if a variable is a number
export function isNumber(n) {
    return /^-?[\d.]+(?:e-?\d+)?$/.test(n);
}

export function toCSS(str) {
    let match = str.match(/\[\s*(\d\.|\d*\.?\d+)\s*,\s*(\d\.|\d*\.?\d+)\s*,\s*(\d\.|\d*\.?\d+)\s*\]/);
    if (match) {
        str = 'rgb(' + Math.round(match[1] * 255) + ',' +
                        Math.round(match[2] * 255) + ',' +
                        Math.round(match[3] * 255) + ')';
    }
    else if (isNumber(str)) {
        let val = Math.round(parseFloat(str) * 255);
        str = 'rgb(' + val + ',' + val + ',' + val + ')';
    }
    else if (/^\s*[\'|\"]#[0-9a-f]{3}(?:[0-9a-f]{3})?[\'|\"]\s*$/i.test(str)) {
        let value = /[\'|\"]([\w|\W|\s]+)[\'|\"]/gm.exec(str);
        return value ? value[1] : '';
    }
    return str;
}

export class StopWatch {
    constructor (performance) {
        this.startTime = 0;
        this.stopTime = 0;
        this.running = false;
        this.performance = performance === false ? false : !window.performance;
    }

    currentTime() {
        return this.performance ? window.performance.now() : new Date().getTime();
    }

    start() {
        this.startTime = this.currentTime();
        this.running = true;
    }

    stop() {
        this.stopTime = this.currentTime();
        this.running = false;
    }

    getElapsedMilliseconds() {
        if (this.running) {
            this.stopTime = this.currentTime();
        }
        return this.stopTime - this.startTime;
    }

    getElapsedSeconds() {
        return this.getElapsedMilliseconds() / 1000;
    }

    printElapsed(name) {
        let currentName = name || 'Elapsed:';
        console.log(currentName, '[' + this.getElapsedMilliseconds() + 'ms]', '[' + this.getElapsedSeconds() + 's]');
    }
}
